<template>
    <div class="SettingName">
      <!---->
      <header class="mui-bar mui-bar-nav">
        <router-link :to="{name:'Setting'}" class="mui-icon mui-icon-left-nav mui-pull-left"></router-link>
        <h1 class="mui-title"></h1>
        <router-link :to="{name:'index'}" class="mui-icon mui-icon mui-icon-home mui-pull-right"></router-link>
      </header>
      <div class="mui-content">
        <form class="mui-input-group setting-name">
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" disabled="disabled">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="name" placeholder="">
          </div>
        </form>
        <div class="form-button">
          <button type="button" class="mui-btn mui-btn-black" @click="setting"></button>
        </div>
      </div>
    </div>

</template>

<script>
export default {
  name: 'SettingName',
  data () {
    return {
      name: ''// 
    }
  },
  methods: {
    setting () {
      var then = this
      var check = true
      var reg = /^[^\s]*$/ // 
      if (this.name == '') {
        mui.toast('')
        check = false
        return false
      }
      if (!reg.test(this.name)) {
        mui.toast('')
        check = false
        return false
      }
      var add = '?name=' + this.name
      this.axios.get('' + add).then(res => {
        if (res.data.msg === '') {
          mui.alert('', function () {
            then.$router.push({ name: 'login' })
          })
        } else {
          mui.alert('')
        }
      })
    }
  }
}
</script>

<style scoped>
  .setting-name{margin-bottom: 22px}
  /**/
  .mui-btn-blue, .mui-btn-black, input[type=submit]{border: 1px solid #000000;background-color: #000000;color: white;width: 70%;}
  .form-button{text-align: center;}
</style>
