<template>
    <div class="account_translation_one">
      <!---->
      <header class="mui-bar mui-bar-nav">
        <router-link :to="{name:'money_receivable'}" class="mui-icon mui-icon-left-nav mui-pull-left"></router-link>
        <h1 class="mui-title"></h1>
        <router-link :to="{name:'index'}" class="mui-icon mui-icon mui-icon-home mui-pull-right"></router-link>
      </header>
      <!---->
      <div class="mui-content">
        <div class="customer">
          <h2></h2>
          <p>/Balance input</p>
        </div>
      </div>
      <!--form-->
      <div class="mui-content app">
        <form class="mui-input-group">
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="cumoterName" disabled="disabled">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="person" disabled="disabled">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="text" disabled="disabled">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="date" class="mui-input-clear" v-model="date">
          </div>
        </form>
        <form class="mui-input-group">
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="all_money" disabled="disabled">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="smoney">
          </div>
        </form>
        <div class="form-botton">
          <button type="button" class="mui-btn mui-btn-black" @click="add"></button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'account_translation_one',
  data () {
    return {
      list: '',
      all_money: '',
      smoney: '',
      cumoterName: '',
      person: '',
      text: '',
      date: '',
      id: ''
    }
  },
  created () {
    var loc = location.href
    var n1 = loc.length// 
    var n2 = loc.indexOf('=')// =
    var id = decodeURI(loc.substr(n2 + 1, n1 - n2))// =
    // 
    this.fund_details_id = id
    this.list = id.split('=')
    var lists = []
    for (var index in this.list) {
      var a1 = this.list[index].indexOf('&')
      var a2 = this.list[index].slice(0, a1)
      lists.push(a2)
    }
    this.id = lists[0]
    this.all_money = lists[1]
    this.smoney = lists[1]
    this.cumoterName = lists[2]
    this.person = lists[3]
    this.text = lists[4]
    this.date = lists[5]
  },
  methods: {
    add () {
      var then = this
      var check = true
      var nuber = /^[0-9]*$/ // 
      // data
      if (this.date == '') {
        mui.toast('')
        check = false
        return false
      }
      // 
      if (this.smoney == '') {
        mui.toast('')
        check = false
        return false
      }
      if (!nuber.test(this.smoney)) {
        mui.toast('')
        check = false
        return false
      }
      var add = 'money=' + this.smoney + '&fund_details_id=' + this.id + '&date=' + this.date
      this.axios.get('https://formattingclub.com/YiNuoLogin/fund/add_fund_details?' + add).then(res => {
        mui.alert(res.data, function () {
          then.$router.push({ name: 'money_receivable' })
        })
      })
    }
  }
}
</script>

<style scoped>
  @import "../css/public.css";
  .mui-input-group{margin-bottom: 10px;}
  /**/
  .mui-btn-blue, .mui-btn-black, input[type=submit]{border: 1px solid #000000;background-color: #000000;color: white;width: 70%;}
  .form-botton{text-align: center;}
</style>
