<template>
    <div class="announcement_entry">
      <!---->
      <header class="mui-bar mui-bar-nav">
        <router-link :to="{name:'company_announcement'}" class="mui-icon mui-icon-left-nav mui-pull-left"></router-link>
        <h1 class="mui-title"></h1>
        <router-link :to="{name:'index'}" class="mui-icon mui-icon mui-icon-home mui-pull-right"></router-link>
      </header>
      <!---->
      <div class="one-noble">
        <h2></h2>
        <p>administration entry</p>
      </div>
      <!--input-->
      <div class="mui-content app">
        <form class="mui-input-group">
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="notice_title">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="notice_number">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" placeholder="" v-model="notice_person">
          </div>
          <div class="mui-input-row form-textarea row-textarea">
            <label></label>
            <textarea name="Customer_demand" rows="" cols="" v-model="notice_text" placeholder=""></textarea>
          </div>
        </form>
        <div class="mui-input-row form-btn">
          <button type="button" id="btn" class="mui-btn mui-btn-blue" @click="go"></button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'announcement_entry',
  data () {
    return {
      notice_title: '', // 
      notice_number: '', // 
      notice_person: '', // 
      notice_text: ''// 
    }
  },
  methods: {
    go () {
      var then = this
      var check = true
      //  
      if (this.notice_title == '') {
        mui.toast('')
        check = false
        return false
      }
      //  
      if (this.notice_number == '') {
        mui.toast('')
        check = false
        return false
      }
      //  
      if (this.notice_person == '') {
        mui.toast('')
        check = false
        return false
      }
      //  
      if (this.notice_text == '') {
        mui.toast('')
        check = false
        return false
      }
      this.axios.get('https://formattingclub.com/YiNuoLogin/notice/addNotice?notice_title=' + this.notice_title + '&notice_number=' + this.notice_number +
        '&notice_person=' + this.notice_person + '&notice_text=' + this.notice_text).then(res => {
        mui.alert(res.data, function () {
          then.$router.push({ name: 'company_announcement' })
        })
      })
    }
  }
}
</script>

<style scoped>
  /**/
  .mui-radio{overflow: visible}
  .mui-btn-blue, .mui-btn-primary, input[type=submit]{border: 1px solid #000000;background-color: #000000;}
  .mui-btn-blue.mui-active:enabled, .mui-btn-blue:enabled:active, .mui-btn-primary.mui-active:enabled, .mui-btn-primary:enabled:active, input[type=submit].mui-active:enabled, input[type=submit]:enabled:active{border: 1px solid #000000;background-color: #000000;}
</style>
