<template>
    <div class="bank_card">
      <!---->
      <header class="mui-bar mui-bar-nav">
        <router-link :to="{name:'money_management'}" class="mui-icon mui-icon-left-nav mui-pull-left"></router-link>
        <h1 class="mui-title"></h1>
        <router-link :to="{name:'index'}" class="mui-icon mui-icon mui-icon-home mui-pull-right"></router-link>
      </header>
      <!---->
      <div class="one-noble">
        <h2></h2>
        <p>administration</p>
      </div>
      <!--form-->
      <div class="mui-content app">
        <form class="mui-input-group">
            <div class="mui-input-row">
                <label></label>
                <input type="text" class="mui-input-clear" v-model="bank_number" placeholder="">
            </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="bank_person" placeholder="">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="bank_money" placeholder="">
          </div>
          <div class="mui-input-row">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="bank_bank" placeholder="">
          </div>
          <div class="mui-input-row" id="persion">
            <label></label>
            <input type="text" class="mui-input-clear" v-model="bank_limit" placeholder="">
          </div>
        </form>
        <div class="mui-input-row input-radio">
          <div class="mui-input-row mui-left mui-radio">
            <label></label>
            <input v-model="bank_type" @click="fund_cu" type="radio" value="">
          </div>
          <div class="mui-input-row mui-left mui-radio">
            <label></label>
            <input v-model="bank_type" @click="fund_xin" type="radio" value="">
          </div>
        </div>
        <div class="mui-input-row form-btn">
          <button type="button" id="btn" class="mui-btn mui-btn-blue" @click="go"></button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'bank_card',
  data () {
    return {
      bank_number: '', // 
      bank_bank: '', // 
      bank_money: '', // 
      bank_person: '', // 
      bank_limit: '', // 
      bank_type: ''
    }
  },
  methods: {
    fund_xin () {
      var persion = document.getElementById('persion')
      persion.style.display = 'block'
    },
    fund_cu () {
      var persion = document.getElementById('persion')
      persion.style.display = 'none'
    },
    go () {
      var then = this
      var add = '?bank_number=' + this.bank_number + '&bank_bank=' + this.bank_bank + '&bank_person=' + this.bank_person + '&bank_type=' + this.bank_type +
          '&bank_money=' + this.bank_money/* '&bank_limit='+this.bank_limit */
      var check = true
      var card = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/ // 
      var nameReg = /^[\u4E00-\u9FA5]{2,4}$/ // 
      var yin = /^(\d{16}|\d{19})$/ // 
      var nuber = /^[0-9]*$/ // 
      if (this.bank_type === '') {
        // 
        if (this.bank_number == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!yin.test(this.bank_number)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_bank == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!nameReg.test(this.bank_bank)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_money == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!nuber.test(this.bank_money)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_person == '') {
          mui.toast('')
          check = false
          return false
        }
        add = add + '&bank_limit=0'
      } else if (this.bank_type === '') {
        // 
        if (this.bank_number == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!yin.test(this.bank_number)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_bank == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!nameReg.test(this.bank_bank)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_money == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!nuber.test(this.bank_money)) {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_person == '') {
          mui.toast('')
          check = false
          return false
        }
        // 
        if (this.bank_limit == '') {
          mui.toast('')
          check = false
          return false
        }
        if (!nuber.test(this.bank_limit)) {
          mui.toast('')
          check = false
          return false
        }
        add = add + '&bank_limit=' + this.bank_limit
      }

      this.axios.get('https://formattingclub.com/YiNuoLogin/fund/AddBank' + add).then(res => {
        if (res.data === '') {
          mui.alert('', function () {
            location.reload()
          })
        }
      }, error => {
        mui.alert('', function () {
          then.$router.push({ name: 'index' })
        })
      })
    }
  }
}
</script>

<style scoped>
@import "../css/public.css";
#persion{display: none}
.mui-radio label{width: 100%}
/**/
.input-radio{display: flex;}
.mui-checkbox.mui-left input[type=checkbox], .mui-radio.mui-left input[type=radio]{left: 16px!important;}
.mui-checkbox.mui-left label, .mui-radio.mui-left label{padding-left: 45px;}
.mui-btn-blue, .mui-btn-black, input[type=submit]{border: 1px solid #000000;background-color: #000000;color: white;width: 22%;}
.mui-btn-blue.mui-active:enabled, .mui-btn-blue:enabled:active, .mui-btn-primary.mui-active:enabled, .mui-btn-primary:enabled:active, input[type=submit].mui-active:enabled, input[type=submit]:enabled:active{border: 1px solid #000000;background-color: #000000;}
/**/
.mui-radio{overflow: visible}
.mui-btn-blue, .mui-btn-primary, input[type=submit]{border: 1px solid #000000;background-color: #000000;}
.mui-btn-blue.mui-active:enabled, .mui-btn-blue:enabled:active, .mui-btn-primary.mui-active:enabled, .mui-btn-primary:enabled:active, input[type=submit].mui-active:enabled, input[type=submit]:enabled:active{border: 1px solid #000000;background-color: #000000;}
</style>
